import React from "react";

const Tweet = ({
    tweet = {
      "first":true,
      "last":false,
      "category": "Web Development",
      "account":"ReactJS",
      "content":"React.js is a component based front end library that makes it very easy to build single-page applications or SPAs.",
      "age":"2h",
      "image":"data:image/png;base64,"
}
}) => {
  let card_style = "card wd-middle";
  if (tweet.first) {
    card_style = "card mt-1 wd-top";
  }
  if (tweet.last) {
    card_style = "card mb-3 wd-bottom";
  }

  return (<div className={`${card_style}`}>
            <div className="row card-body p-1 m-1 ">
              <div className="col-9">
                <p className="wd-content-ends"> {tweet.category}</p>
                <p className="wd-content">{tweet.account} <i className="fa-solid fa-check-circle"/><span
                    className="wd-content-ends"> - {tweet.age}</span></p>
                <p className="wd-content">{tweet.content}</p>
              </div>
              <div className="col-3"><img
                  src={`${tweet.image}`}
                  className="wd-square-image wd-float-right" alt={""}/>
              </div>
            </div>
          </div>
  );
}

export default Tweet;